<script>
  import { afterUpdate } from 'svelte'
  import Chart from 'chart.js/auto';

  let chart

  function createChart(ctx, config) {
    let context = document.getElementById(ctx)
    return new Chart(context, config)
  }

  afterUpdate(() => {
    let config = {
      type: "line",
      data: {
        labels: ['Januari', 'Febuari', 'Maret', 'April', 'Mei', 'Juni', 'juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'],
        datasets: [
          {
            label: 'Transaksi',
            borderColor: '#9696eb',
            backgroundColor: 'rgba(163, 219, 253, 0.2)',
            pointBackgroundColor: '#9696eb',
            pointBorderColor: '#fff',
            pointHoverBackgroundColor: '#fff',
            pointHoverBorderColor: 'rgb(163, 219, 253)',
            data: [120, 330, 410, 223, 566, 516, 313, 856, 554, 789, 503, 40]
          }
        ]
      },
      options: {
        plugins: {
          title: {
            display: true,
            text: 'Transaksi pada tahun 2021'
          }
        },
        scales: {
          y: {
            beginAtZero: true,
            min: 0
          }
        }
      }
    }

    chart = createChart('chart', config)
  })
</script>

<div class="chart-wrapper">
  <canvas id="chart" class="chart"></canvas>
</div>

<style>
  .chart-wrapper {
    padding: 20px;
    height: 100%;
    width: 100%;
  }
</style>
